<template>
    <i-article>
        <article>
            <h1>Icon </h1>
            <inAnchor title="" h2></inAnchor>
            <p>iView <a href="http://ionicons.com/" target="_blank"><Icon type="ios-ionic"></Icon> ionicons</a> 3.x </p>
            <inAnchor title="" h2></inAnchor>
            <p><code>&lt;Icon /></code><code>type</code></p>
            <i-code lang="html" bg>{{ code.demo }}</i-code>
            <p></p>
            <i-code lang="html" bg>{{ code.render }}</i-code>
            <div class="api">
                <inAnchor title="API" h2></inAnchor>
                <inAnchor title="Icon props" h3></inAnchor>
                <table>
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>type</td>
                            <td></td>
                            <td>String</td>
                            <td>-</td>
                        </tr>
                        <tr>
                            <td>size</td>
                            <td> px</td>
                            <td>Number | String</td>
                            <td>-</td>
                        </tr>
                        <tr>
                            <td>color</td>
                            <td></td>
                            <td>String</td>
                            <td>-</td>
                        </tr>
                        <tr>
                            <td>custom</td>
                            <td></td>
                            <td>String</td>
                            <td>-</td>
                        </tr>
                    </tbody>
                </table>
            </div>

            <Demo title="">
                <div slot="demo">
                    <Icon custom="i-icon i-icon-shop_fill" size="24" />
                    <Icon custom="i-icon i-icon-publishgoods_fill" size="24" />
                    <Icon custom="i-icon i-icon-financial_fill" size="24" />
                </div>
                <div slot="desc">
                    <p>iView  Icon </p>
                </div>
                <i-code lang="html" slot="code">{{ code.custom }}</i-code>
            </Demo>

            <Alert show-icon>
                <template slot="desc">iView 3  ionicons 3.x iView 2  ionicons 2.x  iView 2  iView 3</template>
            </Alert>
            <inAnchor title="" h2></inAnchor>
            <div class="icon-search">
                <input
                    type="text"
                    :placeholder="searchPlaceholder"
                    @focus="changeHolder('focus')"
                    @blur="changeHolder('blur')"
                    v-model="search">
                <p></p>
            </div>
            <div class="icons">
                <div class="icons-item" v-for="item in filterIcons" @click="clip(item)">
                    <i class="ivu-icon" :class="[`ivu-icon-${item.name}`]" style="font-size:32px"></i>
                    <p>
                        {{ item.name }}
                    </p>
                </div>
            </div>
        </article>
    </i-article>
</template>
<script>
    import iArticle from '../../components/article.vue';
    import iCode from 'iCode';
    import Demo from '../../components/demo.vue';
    import Code from '../../code/icon';
    import inAnchor from '../../components/anchor.vue';
    import Icons from '../../code/iconname';
    import Clipboard from 'clipboard';

    const searchPlaceholder = ' success';

    export default {
        components: {
            iArticle,
            iCode,
            Demo,
            inAnchor
        },
        data () {
            return {
                code: Code,
                icons: Icons,
                searchPlaceholder: searchPlaceholder,
                search: ''
            }
        },
        computed: {
            filterIcons () {
                return this.icons.filter(icon => icon.tag.indexOf(this.search) > -1);
            }
        },
        methods: {
            clip (type) {
                const icon = `<Icon type="${type.name}" />`;

                const clipboard = new Clipboard('.icons', {
                    text () {
                        return icon
                    }
                });

                clipboard.on('success', (e) => {
                    e.clearSelection();
                    clipboard.destroy();
                    this.$Message.success('');
                });
            },
            changeHolder (type) {
                if (type == 'focus') {
                    this.searchPlaceholder = '';
                } else if (type == 'blur') {
                    this.searchPlaceholder = searchPlaceholder;
                }
            }
        }
    }
</script>


